* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

body {
  color: #333;
  background-color: rgb(250, 255, 176);
}

.list {
  max-width: 640px;
  width: calc(100% - 20px);
  height: 500px;
  margin: 100px auto;
  padding-top: 50px;
  background-color: #FFF;
  text-align: center;
  border-radius: 20px;
  overflow: hidden;
}

.list h2 {
  margin-bottom: 20px;
  font-size: 40px;
}

.list form input {
  max-width: 400px;
  width: calc(100% - 20px);
  height: 40px;
  border: 2px solid #fcc01c;
  border-radius: 10px;
}

.list form button {
  margin-left: 10px;
  height: 40px;
  width: 50px;
  background-color: #fcc01c;
  border: none;
  color: #fff;
  border-radius: 10px;
  cursor: pointer;
}

.list ul {
  margin-top: 20px;
  margin-left: 95px;
  text-align: left;
}

.list ul li {
  margin-bottom: 5px;
}

.list ul li:nth-child(odd)::before {
  content: 'üòù';
  position: relative;
  left: -5px;
  top: 0;
}

.list ul li:nth-child(even)::before {
  content: 'üíñ';
  position: relative;
  left: -5px;
  top: 0;
}

.list ul li:nth-child(3n)::before {
  content: 'üê∞';
  position: relative;
  left: -5px;
  top: 0;
}


/* nth-child(2n) =>2Î∞∞Ïàò
nth-child(odd) =>ÌôÄÏàò
nth-child(even) =>ÏßùÏàò 
 */


@media screen and (max-width:500px) {
  .list form button {
    display: block;
    margin: 10px auto;
    height: 40px;
    width: 50px;
    background-color: #fcc01c;
    color: #fff;
    border-radius: 10px;
    cursor: pointer;
  }

  .list ul {
    margin-top: 20px;
    margin-left: 20px;
  }
}